import "./index.scss";

import React from "react";

import { BoldLacaText } from "../../atoms";
import { Opinion } from "../../organisms";

const OpinionBlock = () => {
  return (
    <div className="opinions--wrapper">
      <div className="opinions-title">
        <BoldLacaText text={"Opinie"} />
      </div>
      <div className="opinions">
        <Opinion
          name={"Pawe"}
          text={
            "Wybrany przez unilaw prawnik pomg mi i nakierowa mnie na odpowiednie rozwizanie. Napisa owiadczenie i pomg w spisaniu zasad."
          }
        />
        <Opinion
          name={"Kasia"}
          text={
            "Profesjonalne podejcie do klienta. Sprawa, ktr miaam wydawaa si beznadziejna. Dziki wskazanemu freelancerowi sprawa skoczya si pozytywnie. Polecam serdecznie."
          }
        />
        <Opinion
          name={"Kasia"}
          text={
            "Profesjonalne podejcie do klienta. Sprawa, ktr miaam wydawaa si beznadziejna. Dziki wskazanemu freelancerowi sprawa skoczya si pozytywnie. Polecam serdecznie."
          }
        />
      </div>
    </div>
  );
};

export default OpinionBlock;
