import "./index.scss";

import React from "react";

import { BoldLacaText } from "../../atoms";
import { CardInfo } from "../../molecules";

const CardInfoBlock = () => {
  return (
    <div className="card-info--wrapper">
      <div className="card-info-title">
        <BoldLacaText text={"Bd tam, gdzie Twoi klienci"} />
      </div>
      <div className="card-info-cards">
        <CardInfo
          link={require("../../../assets/vector/steps.svg").default}
          text={
            "W 2019 roku a 61% konsumentw doceniao wygod i atwo zakupu usugi. W 2020 roku warto ta wzrosa jeszcze bardziej - o 41%."
          }
        />
        <CardInfo
          link={require("../../../assets/vector/browser.svg").default}
          text={
            "Wikszo osb szuka specjalistw w sieci. Jeli nie bdziesz dostpny online, pjd do Twojej konkurencji."
          }
        />
        <CardInfo
          link={require("../../../assets/vector/laptop.svg").default}
          text={
            "Rezerwacja konsultacji jeszcze nigdy nie bya tak prosta! Wykorzystaj t szans i zacznij zarabia wicej z Unilaw."
          }
        />
      </div>
    </div>
  );
};

export default CardInfoBlock;
